/* src/components/Cart.css */

.cart {
    padding: 20px;
    max-width: 800px;
    margin: auto;
  }
  
  .empty-cart {
    text-align: center;
    font-size: 1.2em;
    color: #888;
    padding: 40px 0;
  }
  
  .cart-item {
    display: flex;
    align-items: center;
    padding: 15px;
    margin-bottom: 10px;
    border: 1px solid #ddd;
    border-radius: 8px;
  }
  
  .cart-item-image {
    width: 80px;
    height: 80px;
    border-radius: 5px;
    object-fit: cover;
    margin-right: 15px;
  }
  
  .cart-item-info {
    flex-grow: 1;
  }
  
  .cart-item-controls {
    display: flex;
    align-items: center;
  }
  
  .cart-item-controls button {
    padding: 5px 10px;
    margin: 0 5px;
    cursor: pointer;
  }
  
  .remove-button {
    background-color: #dc3545;
    color: white;
    border: none;
    margin-top: 1rem;
    padding: 5px 10px;
    border-radius: 5px;
    cursor: pointer;
  }
  
  /* src/components/Cart.css */
.cart-total {
  margin-top: 20px;
  text-align: right; /* Aligns the total to the right */
}

.place-order-button {
  padding: 10px 20px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s ease;
}

.place-order-button:hover {
  background-color: #218838;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background-color: white;
  padding: 20px;
  border-radius: 8px; /* Border radius for Material Design look */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  width: 300px; /* Adjust width as needed */
}

.modal-content h2 {
  margin-top: 0;
}

.modal-content form {
  display: flex;
  flex-direction: column;
}

.modal-content input {
  margin: 10px 0;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.modal-content button {
  padding: 10px;
  margin-top: 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.modal-content button[type="submit"] {
  background-color: #4CAF50;
  color: white;
}

.modal-content button[type="button"] {
  background-color: #f44336;
  color: white;
}

.error {
  color: red;
  font-size: 0.8em;
}
